</template>

<script>
import firebase from 'firebase';
import $ from 'jquery';

export default {
  props: ['userData', 'detail'],
  data() {
    return {
      isLoading: false,
      fackUserDara: {},
      tempDom: {
        editInput: '',
        uploading: '',
      }
    }
  },
  watch: {
    'detail.bio': {
      handler: function autogrow(val) {
        const textarea = document.getElementById('bio');
        let adjustedHeight = textarea.clientHeight;
        adjustedHeight = Math.max(textarea.scrollHeight,adjustedHeight);
        if (adjustedHeight > textarea.clientHeight) {
          textarea.style.height=adjustedHeight+'px';
        }
      },
    },
  },
  methods: {
    preview(e) {
      const vm = this;
      const file = e.target.files[0];
      vm.tempDom.imgurl = URL.createObjectURL(file);
    },
    cvs() {
      const vm = this;
      let imgNewWidth = 600, // 圖片新寬度
      canvas = document.createElement("canvas"),
      img = new Image(),
      context = canvas.getContext("2d");
      
      let imgfile = $('#scream'),
      imgH = imgfile.height(),
      imgW = imgfile.width(),
      imgNewHeight = imgNewWidth * imgH / imgW, // 圖片新高度
      newImg

      img.src = vm.tempDom.imgurl;
      canvas.width = imgNewWidth;
      canvas.height = imgNewHeight;
      context.clearRect(0, 0, imgNewWidth, imgNewHeight);
      context.drawImage(img, 0, 0, imgNewWidth, imgNewHeight); // 調整圖片尺寸
      newImg = canvas.toDataURL("image/jpeg", 1);
      return newImg
    },
    uploadImg() {
      const vm = this;
      vm.isLoading = true;
      var timestamp = Number(new Date()); // 檔名設定
      let fileName = timestamp.toString() + vm.userData.uid;
      var storageRef = firebase.storage().ref('user_photo/' + fileName); //資料夾+黨名
      var file = vm.dataURLtoFile(vm.cvs(), 'fileName');
      storageRef.put(file)
      .then((snapshot) => {
        // 取得圖片位置
        snapshot.ref.getDownloadURL().then(function(downloadURL) {
          vm.uploadImgData(downloadURL)
        });
      })
      .catch((error) => {
        alert('上傳失敗，請重新整理後重試');
        console.log(error)
      });
    },
    dataURLtoFile(dataurl, filename) {
      var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, {type:mime});
    },
    saveChange(item) {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/editAccountData`;
      let reqData = {
        path: 'detail',
        item: item,
        val: vm.detail[item],
        uid: vm.userData.uid,
      }
      if(vm.tempDom.editInput === 'displayName') {
        reqData.path = 'basic';
        reqData.val = vm.fackUserDara.displayName;
      }
      this.$validator.validate().then((result) => {
        if (result) {
          vm.$http.post(url, {"reqData": reqData}).then((response) => {
            if(vm.tempDom.editInput === 'displayName') {
              window.location.reload(true);
            }
            vm.tempDom.editInput = '';
          })
          .catch((err) => {
            alert('更新失敗，建議重新整理後重試');
          })
        }
      })
    },
    getUserDetail(user) {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getData`;
      vm.$http.post(url, {"url": `users/${user.uid}/detail/`}).then((response) => {
        if(response.data) {
          vm.detail = response.data;
        }
      })
    },
    uploadImgData(downloadURL) {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/editAccountData`;
      let reqData = {
        path: 'basic',
        item: 'photoURL',
        val: downloadURL,
        uid: vm.userData.uid,
      }
      // console.log(reqData)
      vm.$http.post(url, {"reqData": reqData}).then((response) => {
        window.location.reload(true);
      })
      .catch((err) => {
        alert('更新失敗，建議重新整理後重試');
      })
    },
  },
  created() {
    const vm = this;
    vm.fackUserDara = Object.assign({}, vm.userData);
  },
}
</script>

<style lang="scss" scoped>
#upload-img{
  background-color: rgba(0,0,0,0.5);
}
textarea{
  resize: none;
  &::-webkit-scrollbar {
    width: 0px;
  }
  &::-webkit-scrollbar-track {
    background: transparent
  }
  &::-webkit-scrollbar-thumb {
    background-color: transparent;
  }
  &::-webkit-scrollbar-thumb:hover {
    background-color: transparent;
  }
}
.files{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px 0;
  cursor: pointer;
  input{
    border-radius: 5px;
    border: 1px solid #ccc;
    margin-top: 10px;
    padding: 5px;
    max-width: 100%;
  }
  .select-img{
    width: 225px;
  }
  .img-preview{
    height: 300px;
    width: 100%;
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px dashed #ccc;
    border-radius: 5px;
    img{
      max-width: 600px;
      position: absolute;
    }
  }
}
.photo {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  flex-direction: column;
  .pic{
    width: 150px;
    height: 150px;
    background-size: cover;
    border-radius: 50%;
    background-position: center center;
    border: 10px solid rgba(#fff, .7);
    box-sizing: border-box;
    margin-bottom: 5px;
  }
  p{
    cursor: pointer;
    color: #1082c9;
  }
}
p, label{
  margin-bottom: 0;
}
.title{
  margin-top: 30px;
  border-left: 15px solid #fecd88;
  font-size: 20px;
  font-weight: bold;
  padding-left: 10px;
  line-height: 20px;
  margin-bottom: 10px;
}
.form-group{
  display: flex;
  align-items: center;
  .d-flex{
    flex: 1;
  }
  .defalt-data{
    padding: 5px 10px;
    border-bottom: 1px dashed #ccc;
    flex: 1;
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;
    &.no-pointer{
      cursor: default;
    }
    .edit-btn{
      // position: absolute;
      border: none;
      background: transparent;
      color: #555;
      margin-left: 10px;
      transition: all 0.2s ease-in-out;
    }
    @media (min-width: 770px){
      .edit-btn{
        z-index: -1;
        opacity: 0;
      }
      &:hover{
        .edit-btn{
          z-index: 1;
          opacity: 1;
        }
      }
    }
  }
  .nn{
    white-space: nowrap;
    svg{
      color: #ccc;
      margin-right: 5px;
    }  
  }
  label {
    margin-right: 5px;
    white-space: nowrap;
    font-weight: normal;
  }
}
</style>
